// Registration.js
import React, { useState } from "react";
import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword, signInWithPopup, GoogleAuthProvider } from "firebase/auth";
import { useNavigate } from "react-router-dom"; // Hook for navigation
import { auth } from "./firebaseConfig"; // Import your Firebase authentication instance
import './Registration.css';

const Registration = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [isSignUp, setIsSignUp] = useState(false); // Toggle between Sign Up and Log In
  const navigate = useNavigate(); // Hook to navigate after login or registration

  // Google Authentication
  const handleGoogleSignUp = async () => {
    const provider = new GoogleAuthProvider();

    try {
      const result = await signInWithPopup(auth, provider);
      // User is signed in with Google
      navigate("/search"); // Navigate to /search after successful login/signup
    } catch (error) {
      setError(error.message);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      if (isSignUp) {
        // Sign up logic
        await createUserWithEmailAndPassword(auth, email, password);
        navigate("/search"); // Navigate to /search after successful sign-up
      } else {
        // Sign-in logic
        await signInWithEmailAndPassword(auth, email, password);
        navigate("/search"); // Navigate to /search after successful login
      }
    } catch (error) {
      setError(error.message); // Set error message if login or registration fails
    }
  };

  return (
    <div className="registration-container">
      <h2>{isSignUp ? "Sign Up" : "Log In"}</h2>
      <form onSubmit={handleSubmit}>
        <input
          type="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="Enter your email"
        />
        <input
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="Enter your password"
        />
        <button type="submit">{isSignUp ? "Sign Up" : "Log In"}</button>
      </form>
      {error && <p className="error-message">{error}</p>}
      
      <div className="toggle">
        <p>{isSignUp ? "Already have an account?" : "Don't have an account?"}</p>
        <button onClick={() => setIsSignUp((prev) => !prev)}>
          {isSignUp ? "Log In" : "Sign Up"}
        </button>
      </div>

      {/* Google Sign-Up Button */}
      <button onClick={handleGoogleSignUp} className="google-signup-button">
        Sign Up with Google
      </button>
    </div>
  );
};

export default Registration;
