import React, { useState, useEffect } from "react";
import DOMPurify from "dompurify";
import { ref, get, update } from "firebase/database";
import { auth, db } from "./firebaseConfig"; // Import db and auth
import "./RecipeDetails.css";
import  FirebaseRecipeManager from "./FirebaseRecipeManager" 
const RecipeDetails = ({ recipe, onBack }) => {
  const {
    id,
    image,
    title,
    readyInMinutes,
    servings,
    extendedIngredients,
    instructions,
  } = recipe || {};

  const [rating, setRating] = useState(0);
  const [averageRating, setAverageRating] = useState(null);
  const [userRating, setUserRating] = useState(null);

  const user = auth.currentUser;

  const sanitizedInstructions = DOMPurify.sanitize(
    instructions || "No instructions provided."
  );

  useEffect(() => {
    const fetchRatings = async () => {
      console.log("Fetching ratings for recipe ID:", id);
      const recipeRef = ref(db, `recipes/${id}`);
      const recipeSnapshot = await get(recipeRef);

      if (recipeSnapshot.exists()) {
        const recipeData = recipeSnapshot.val();
        console.log("Fetched recipe data:", recipeData);

        // Fetch and set the user's previous rating
        setUserRating(recipeData.ratings?.[user?.uid] || null);

        // Calculate and set the average rating
        const ratingsArray = Object.values(recipeData.ratings || []);
        const average =
          ratingsArray.reduce((sum, rating) => sum + rating, 0) /
          ratingsArray.length;
        console.log("Calculated average rating:", average);
        setAverageRating(average);
      } else {
        console.log("No data found for recipe ID:", id);
      }
    };

    if (id) {
      fetchRatings();
    }
  }, [id, user?.uid]);

  const handleRating = async (newRating) => {
    if (!user) {
      alert("Please log in to rate this recipe.");
      return;
    }

    console.log("User is rating:", newRating, "for recipe ID:", id);

    const recipeRef = ref(db, `recipes/${id}`);

    // Save the new rating
    await update(recipeRef, {
      [`ratings/${user.uid}`]: newRating,
    });
    console.log("Rating saved to Firebase.");

    // Recalculate average rating
    const recipeSnapshot = await get(recipeRef);
    if (recipeSnapshot.exists()) {
      const recipeData = recipeSnapshot.val();
      console.log("Fetched recipe data after update:", recipeData);

      const ratingsArray = Object.values(recipeData.ratings || []);
      const average =
        ratingsArray.reduce((sum, rating) => sum + rating, 0) /
        ratingsArray.length;

      // Update the average rating in Firebase
      await update(recipeRef, { averageRating: average });
      console.log("Updated average rating in Firebase:", average);

      // Update the UI
      setAverageRating(average);
    }
  };

  return (
    <div className="recipe-details-container">
      <button onClick={onBack} className="back-button-style">
        ← Go Back
      </button>
      
      <div className="recipe-header-section">
        <img src={image} alt={title} className="recipe-image-style" />
        <h1 className="recipe-title-style">{title}</h1>
      </div>
      <div className="recipe-meta-section">
        <p>
          <strong>Time to Cook:</strong> {readyInMinutes} minutes
        </p>
        <p>
          <strong>Serves:</strong> {servings}
        </p>
      </div>
      <div className="ingredients-section">
        <h2>Ingredients</h2>
        <ul>
          {extendedIngredients?.map((ingredient, index) => (
            <li key={index}>{ingredient.original}</li>
          ))}
        </ul>
      </div>
      <div className="instructions-section">
        <h2>Cooking Instructions</h2>
        <div
          dangerouslySetInnerHTML={{ __html: sanitizedInstructions }}
        ></div>
      </div>
      <div className="rating-section">
        <h3>Rate this Recipe</h3>
        <div className="rating-stars">
          {[1, 2, 3, 4, 5].map((star) => (
           <span
           key={star}
           onClick={() => handleRating(star)}
           className={`star ${rating >= star ? "filled" : ""}`}
         >
           ★
         </span>
          ))}
        </div>
        <div className="average-rating">
          {averageRating !== null ? (
            <p>Average Rating: {averageRating.toFixed(1)} / 5</p>
          ) : (
            <p>No ratings yet</p>
          )}
        </div>
        {userRating !== null && <p>Your Rating: {userRating} / 5</p>}
      </div>
    </div>
  );
};

export default RecipeDetails;
