import React, { useEffect, useState } from "react";
import { HashRouter as Router, Routes, Route, Navigate } from "react-router-dom"; // Use HashRouter
import { getAuth } from "firebase/auth"; // Import Auth from Firebase
import RecipeSearch from "./RecipeSearch";
import RecipeDetails from "./RecipeDetails";
import Registration from "./Registration";
import { auth } from "./firebaseConfig"; // Correct import from firebaseConfig.js

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    // Ensure authentication is being managed correctly
    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (user) {
        console.log("User is logged in: ", user);
        setIsAuthenticated(true);
      } else {
        console.log("User is not logged in");
        setIsAuthenticated(false);
      }
    });

    // Cleanup function for the auth listener
    return () => unsubscribe();
  }, []);

  return (
    <Router>
      <Routes>
        <Route path="/login" element={<Registration />} />

        {/* Protected Route for Recipe Search */}
        <Route
          path="/search"
          element={isAuthenticated ? <RecipeSearch /> : <Navigate to="/login" />}
        />

        {/* Protected Route for Recipe Details */}
        <Route
          path="/recipe/:id"
          element={isAuthenticated ? <RecipeDetails /> : <Navigate to="/login" />}
        />

        {/* Default Route - Redirect to /search if authenticated, otherwise to /login */}
        <Route
          path="/"
          element={isAuthenticated ? <Navigate to="/search" /> : <Navigate to="/login" />}
        />
      </Routes>
    </Router>
  );
};

export default App;
